* {
  outline: none;
}

.App {
  background-color: white;
}

.no-session {
  width: 100%;
}

#right-triangle {
  float: right;
  position: absolute;
  bottom: 0;
  shape-outside: polygon(0 70%, 100% 5%, 100% 100%, 0% 100%);
  clip-path: polygon(0 72%, 100% 41%, 100% 100%, 0% 100%);
  background: rgb(139, 84, 255);
  height: 100%;
  width: 100%;
}

.diamond {
  background: rgb(214, 194, 255);
  /* border: solid 2em rgb(214, 194, 255); */
  /* float: left; */
  position: absolute;
  transform: rotate(45deg);
  shape-outside: inset() padding-box;
  clip-path: inset() padding-box;
  padding: 2em;
  width: 15em;
  height: 15em;
}

.small {
  width: 50px;
  height: 50px;
  z-index: 3;
  background-color: rgb(255, 246, 223);
}
